<template>
  <div class="rounded flex justify-between" :class="colours">
    <div class="p-4">
      <span class="font-bold">{{ modeName }}</span
      ><span v-if="started"> - {{ timeLeft }}</span>
    </div>
    <button
      class="shadow-xl p-4 bg-green-100 text-green-800 rounded-r"
      v-if="ended"
      @click="reset"
    >
      Restart
    </button>
    <span class="p-4" v-else>Session {{ session }}/{{ totalSessions }}</span>
  </div>
</template>

<script>
import modes from '../services/modes';

export default {
  name: 'Mode',
  props: {
    mode: {
      type: Number,
    },
    secondsLeft: {
      type: Number,
    },
    session: {
      type: Number,
    },
    totalSessions: {
      type: Number,
    },
  },

  methods: {
    reset() {
      this.$emit('reset');
    },
  },

  computed: {
    modeName() {
      const names = {
        [modes.READY]: 'Get ready...',
        [modes.WRITING]: 'SPRINT!',
        [modes.EDITING]: 'Pause, edit, relax',
        [modes.DIED]: 'What happened? Wh-where is my chapter??',
        [modes.COMPLETE]: 'All done!',
      };
      return names[this.mode];
    },
    colours() {
      const colours = {
        [modes.READY]: 'bg-blue-700 text-blue-100',
        [modes.WRITING]: 'text-red-100 bg-red-700',
        [modes.EDITING]: 'bg-green-700 text-green-100',
        [modes.DIED]: 'text-gray-50 bg-black',
        [modes.COMPLETE]: 'bg-green-700 text-green-100',
      };
      return colours[this.mode];
    },
    timeLeft() {
      return new Date(this.secondsLeft * 1000).toISOString().substr(14, 5);
    },
    started() {
      return this.mode === modes.WRITING || this.mode === modes.EDITING;
    },
    ended() {
      return (
        this.mode === modes.DIED || this.mode === modes.COMPLETE
      );
    },
  },
};
</script>
